Recommendations.propTypes = {
  duties: PropTypes.bool,
  qualifications: PropTypes.bool,
  portfolio: PropTypes.bool,
  about: PropTypes.bool,
}
Recommendations.defaultProps = {
  duties: false,
  qualifications: false,
  portfolio: false,
  about: false,
}

export default Recommendations
